/*sizes and breakpoints*/
/*retina*/
body {
  width: 99.5%;
  margin-top: 0; }

html {
  box-sizing: border-box; }

img {
  max-width: 100%; }

*,
*::after,
*::before {
  box-sizing: inherit; }

@media (min-width: 501px) {
  .container {
    max-width: 768px;
    margin-left: auto;
    margin-right: auto; } }

@media (min-width: 1300px) {
  .container {
    max-width: 5300px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

