.header {
  height: 700px;
  background-image: url("/img/bg.jpg");
  background-size: 100% 700px;
  background-repeat: no-repeat; }

.header__title {
  color: #e0e0e0; }

.header__nav {
  display: flex;
  justify-content: space-around; }

.header__list {
  width: 50%;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  list-style: none; }

.header__link {
  font-family: "Arial regular";
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 300;
  color: #e0e0e0;
  text-decoration: none; }
  .header__link:hover {
    color: #c6c6c6; }

.services {
  width: 100%;
  height: 800px; }

.services__list {
  position: relative;
  display: flex;
  list-style: none;
  flex-wrap: no-wrap;
  padding-left: 0; }

.services__item {
  width: 17%;
  height: 350px;
  background-size: 100% 100%; }
  .services__item:hover {
    width: 19%;
    height: 370px;
    box-shadow: 0px 7px 30px 0px #323232; }

.services__item1 {
  background-image: url("/img/mt.jpg"); }

.services__item2 {
  background-image: url("/img/szp.jpg"); }

.services__item3 {
  background-image: url("/img/of.jpg"); }

.services__item4 {
  background-image: url("/img/va.jpg"); }

.services__item5 {
  background-image: url("/img/kr.jpg"); }

.services__item6 {
  background-image: url("/img/ff.jpg"); }

.services__title {
  text-transform: uppercase;
  text-align: center;
  font-family: "Arial Regular";
  font-size: 16px;
  font-weight: 300;
  color: white; }

.services__title--black {
  color: black; }
